@media only screen and (min-width: $bpDesktop) {
	.reseller-locator {
		#resellerMap {
			width: 60%;
			min-width: 720px;
			height: 400px;
		}

		.reseller-map {
			max-width: none;

			.switch-view {
				right: auto;
				left: 720px;
				transform: translate(-100%, 0);
			}
		}
	}
}
