// tab-carousel styles
.tab-carousel {
	&__tabs {
		ul {
			@extend %list-unstyled;
			@extend %clearfix;
		}

		li {
			float: left;
			width: auto;
			min-width: 25%;
			text-align: center;
			cursor: pointer;

			&.active {
				a {
					background-color: $red;
					color: #fff;
				}
			}
		}

		a {
			text-decoration: none;
			display: block;
			padding: 5px 0;
			background-color: $border;
			color: #000;
			font-weight: 400;
			font-size: calc-em(20px);

			&:hover {
				text-decoration: none;
			}
		}

