// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  position: relative;
  font-family: $primaryFont;
  line-height: 1.6;
  font-weight: 300;
  word-wrap: break-word;
  color: #000;
  font-size: calc-em(16px);
}

@media print {
  body {
    zoom: 0.85;
  }
}

img {
  vertical-align: top;
  // width: 100%;
  max-width: 100%;
  flex-shrink: 0;
}

h1, h2, h3, h4, h5, h6 { font-weight: 400; }

.timeline-Tweet:hover {
  background-color: rgba(0,0,0,0);
}

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

//removing focus ring for mouse events
.no-focus-outline {
  *:not(.has-outline):focus {
    outline: none;
  }
}

.red-link {
  color: $red;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

/* Layouts */
.layout {
  position: relative;
  max-width: 1220px;
  margin: 80px auto 0;
  padding: 20px 10px 0;
  width: 100%;
}

.main-content, .page-not-found {
  position: relative;
  max-width: 1220px;
  margin: 80px auto 0;
  padding: 20px 10px 0;
}

@media only screen and (max-width: $bpTablet - 1){
  .main-content, .page-not-found {
    > h1, > h2, > h3, > h4, > h5, > h6 {
      &:first-of-type {
        margin: 0;
      }
    }
  }
}

#at15s {
  margin-top: -80px !important;
}

// aside layout
.layout-aside {
  @extend %clearfix;
}
// aside layout

//article layout
.layout-article {
  @extend %clearfix;

  .layout-side {
    width: 100%;
    margin: 0 0 0 24px;
  }
}
// article layout

.top-banner {
  width: 100%;
  margin-bottom: 24px;
}

.layout-main, .layout-bottom {
  // min-width: 300px;
  max-width: 1200px;
  margin: 0 auto;
}

.layout-bottom {
  max-width: 1220px;
  padding: 0 10px;
}

.layout-side {
  > *:first-child {
    // border-top: 0;
    margin-top: 0;
  }
}

// .wingspan {
//   margin: auto;
//   // padding: 0 6px;
//   // max-width: 1220px;
//   box-sizing: border-box;
// }

@media only screen and (max-width: $bpTablet - 1){
  .layout-side--left {
    display: none;
  }

  .layout-article {
    .layout-side {
      width: 100%;
      margin: 24px 0 0;
      max-width: none;
    }
  }
}

@media only screen and (min-width: $bpTablet), print {
  .layout-article {
    .layout-main {
      float: left;
      width: calc(60% - 48px);
      min-width: calc(100% - 528px);
    }

    .layout-side {
      float: left;
      width: 20%;
      max-width: 240px;
      margin: 0 0 0 24px;

      &--left {
        margin: 0 24px 0 0;
      }
    }
  }
}

@media only screen and (min-width: $bpDesktop), print {
  .main-content, .page-not-found {
    margin: 0 auto 0;
    padding: 150px 10px 0;
  }

  #at15s {
    margin-top: 0 !important;
  }

  .layout-aside {
    .layout-main {
      float: left;
      width: calc(100% - 234px);
    }

    .layout-side {
      float: left;
      display: block;
      width: 100%;
      max-width: 210px;
      margin: 0 0 0 24px;
    }

    .layout-bottom {
      float: left;
      width: 100%;
    }
  }
}

@media print {
  .layout-aside {
    .layout-main {
      width: 100%;
    }
    .layout-side {
      width: 100%;
    }
  }
}

