/* Result status */
.result-status {
  text-align: center;

  .msg {
    display: none;
    // background: $grey-3;
    text-align: left;
    padding: 3px 0;
    font-size: calc-em(14px);
    margin: 0;
  }
}

/* Loader animation */
@keyframes fade {
  0%   {opacity: 1;}
  40%   {opacity: 0;}
  60%   {opacity: 0;}
  100% {opacity: 1;}
}

.loader-animation {
  display: none;
  position: relative;
  margin-left: -30px;

  .inner {
    display: block;
    width: 10px;
    height: 10px;
    background: #000;
    position: absolute;
    animation-name: fade;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    top: 0;

    &:nth-child(1) {
      left: 0;
      animation-delay: 0;
    }

    &:nth-child(2) {
      left: 12px;
      animation-delay: 200ms;
    }

    &:nth-child(3) {
      left: 24px;
      animation-delay: 400ms;
    }
  }
}

/* Back to top */
.back-to-top {
  position: fixed;
  right: 0;
  top: 70%;
  padding: 4px 10px;
  background: $border;
  text-align: center;
  cursor: pointer;
  display: none;
  z-index: 5;

  span {
    display: block;
    text-transform: uppercase;
    font-size: 14px;
    margin-top: -10px;
  }

  i {
    font-size: 24px;
    line-height: 1;
  }
}


@media print {
  // Show Only Active Slide
  .slick-slide {
    display: none !important;
    &.slick-active {
        display: block !important;
    }
  }

  // Reset adaptiveHeight
  .slick-list {
    height: auto !important;
  }

  // Remove Scrollbars
  .slick-track {
    width: auto !important;
    height: auto !important;
    transform: none !important;
    .slick-slide {
        width: auto !important;
    }
  }

  .match-height {
    height: auto !important;
  }
}


