// product-finder styles

.product-finder {
    background: $border;
    padding: 20px 5px;

    &__header {
        text-align: center;

        h1 {
            font-size: calc-em(26px);
            line-height: 30px;
            margin-top: 0;
        }
    }

    .question {
        &__desc {
            text-align: center;
            font-size: calc-em(20px);
            font-weight: 500;
        }
    }
    &__footer {
        // display: flex;
        // justify-content: space-between;
        margin: 15px auto;
        position: relative;
        @include clearfix;

        > div {
            margin-bottom: 15px;
        }

        &--back {
            display: inline-block;
            float: left;
            // text-align: center;

            .button {
                // margin: 0 auto;
            }
        }

        &--results {
            display: block;
            text-align: center;
            p {
                background-color: $darkgrey-4;
                padding: 8px 15px;
                border-radius: 7px;
                color: white;
                font-size: calc-em(16px);
                display: inline-block;
                margin: 0;
            }

            
        }

        &--right {
            display:inline-flex;
            justify-content: center;
            float: right;

            button + button {
                margin-left: 15px;
            }
        }

        .btn-findprinter {
            display: none;

            &.is-shown {
                display: flex;
            }
        }

        .btn-compareprinter:disabled, .btn-compareprinter[disabled] {
            background:$grey-5;
            border-color:$grey-5;
            color: $searchBorder;
            cursor: default;
        }
    }

    &__empty {
        text-align: center;
    }

    button {
        line-height: 1;
        text-decoration: none;
        color: #fff;
        background: $red;
        position: relative;
        display: inline-block;
        padding: 4px 10px 4px 10px;
        border: 0;
        display: flex;
        align-items: center;
        cursor: pointer;
        border: 1px solid $red;
        font-family: $primaryFont;
        font-weight: 500;
        padding: 5px 10px 5px 10px;
        &:hover {
            background: white;
            color: $red;
            border: 1px solid $red;
        }
        span {
            font-size: calc-em(16px);
            font-weight: 500;
        }
        span + .icon {
            padding-left: 5px;
        }

        .icon + span {
            padding-left: 5px;
        }
        
        &.btn-white {
            color: black;
            background: white;
            border: 1px solid #D7D7D7;

            &:hover {
                background: $red;
                color: white;
                border: 1px solid $red;
            }
        }

        .icon {
            font-size: 22px;
            line-height: 22px;
        }
        .icon-arrow-left, .icon-arrow-right {
            font-size: 14px;
        }

        .icon-return {
            font-size: 16px;
        }
    }
}
@media only screen and (max-width: $bpTablet - 1) {
    .product-finder {
        padding: 35px 10px 20px;

        &-wrap {
            padding: 0;
        }

        &__footer--right button + button {
            margin-left: 5px;
        }

        button {
            padding: 5px 5px 5px 10px;

            .btn-white {
                padding: 5px 8px 5px 5px;
            }
        }

        &-results .finder-results__desc h2 {
            font-size: calc-em(20px);
        }
    }
}

@media only screen and (min-width: $bpTablet), print{
    .product-finder {
        padding: 50px 25px;

        &__header {
            h1 {
                font-size: calc-em(32px);
            }
        }

        &__footer {
            margin: 35px auto;
            @include clearfix;
            &--back {
                // position: absolute; 
                // left: 0;
                position: relative;
                z-index: 1;
            }
            &--right {
                // position: absolute;
                // right: 0;
                // top: 0;
                // width: auto;
                z-index: 1;
            }

            &--results {
                position: absolute;
                top: -4px;
                left: 0;
                right: 0;
                z-index: 0;
            }
        }

        .question {
            &__desc {
                font-size: calc-em(24px);
            }
        }
    }
}