// product-item styles
.product-item {
    display: inline-block;
    text-align: center;
    position: relative;
    width: 50%;
    float:left;
    padding: 0 8px;
    margin-bottom: 12px;
    
    &__list {
        @include normalise-list;
        @include clearfix;
    }

    &--wrap {
        background: white;
        
        
    }

    &__checkbox {
        margin-bottom: 10px;
        span {
            color: $darkgrey;
            font-size: calc-em(14px);
            padding-right: 25px;
            position: relative;

            &:after {
                content: '';
                display: inline-block;
                border: 1px solid $border4;
                width: 18px;
                height: 18px;
                position: absolute;
                top: -2px;
                right: 0;
                color: white;
            }
        }

        input {
            position: absolute;
            visibility: hidden;

            &:checked + span {
                &:after {
                    content: '\e912';
                    border: 1px solid white;
                    background: $red;
                    font-family: $icomoon;
                    font-size: 14px;
                    line-height: 18px;
                }
            }
        }
    }

    label {
        cursor: pointer;
        display: block;
        padding: 15px 10px 20px;
        p {
            margin: 0;
            color: $darkgrey;
            font-size: calc-em(14px);
            line-height: 18px;

            span {
                display: block;
            }
        }

        h3 {
            margin-bottom: 5px;
        }

        a.button {
            margin: 15px 0 0;
        }
        
    }

    &__img {
        height: 105px;
        
        display: block;
        img {
            @include image-contain;
            // margin: 0 auto;
        }
    }
}

@media only screen and (min-width: $bpTablet), print{
    .product-item {
        width: 33.33333%;

        &__list {
            @include clearfix;
        }

        // &__img {
        //     max-width: 100px;
        // }

        &__checkbox {
            span {
                padding-right: 30px;
                &:after {
                    width: 20px;
                    height: 20px;
                }
            }
        }

        label {
            padding: 15px 15px 20px;
        }
    }
}