// steps-list styles

.steps-list {
    @include normalise-list;
    text-align: center;
    padding: 30px 0;
    display: inline-flex;
    width: 100%;
    justify-content: center;

    li {
        display: inline-block;
        position: relative;
        width: 60px;

        &:before {
            content: '';
            border: 0;
            border-top: 1px solid #ccc;
            width: 40px;
            display: block;
            position: absolute;
            top: 12px;
            left: 70%;
        }

        &:last-child:before {
            display: none;
        }

        &.is-active {
            .num {
                background: $red;
                border: 1px solid $red;
                color: white;
            }

            .txt {
                color: $bannerText;
            }
        }
    }

    .num {

        display: inline-block;
        height: 25px;
        width: 25px;
        border-radius: 50%;
        border: 1px solid $border4;
        // margin-right: 20px;
        background: white;


        span {
            display: block;
            margin-top: 4px;
            font-size: calc-em(12px);
        }
    }
    .txt {
        display:block;
        color: $searchBorder;
        font-size: calc-em(12px);
        line-height: 1.2;
        margin-top: 10px;
    }
}

@media only screen and (min-width: $bpTablet), print{
    .steps-list {
        // width: auto;
        // display: block;
        padding: 10px 0;
        li {
            width: 110px;

            &:before {
                width: 64px;
                top: 24px;
                left: 80px;
            }
        }

        .num {
            height: 50px;
            width: 50px;
            span {
                margin-top: 12px;
                font-size: calc-em(18px);
            }
        }

        .txt {
            font-size: calc-em(15px);
        }
    }
}
