// modal styles
.modal {
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	z-index: 8000;
	overflow: auto;
	-webkit-overflow-scrolling: touch;
	background: rgba(255, 255, 255, .96);

	.modal-wrapper {
		position: relative;
		margin: 30px auto 230px;
  		max-width: 1220px;
	}

	.modal-close-button {
		position: relative;
		text-align: right;
		margin: 20px 20px 10px;

		a {
			display: inline-block;
			width: 30px;
			height: 30px;
			border: 2px solid $red;
			border-radius: 50%;
			text-decoration: none;
			line-height: 30px;
			text-align: center;

			i {
				position: relative;
				color: #000;
				// top: 2px;
			}

			&:hover {
				background: $red;

				i { color: #fff; }
			}
		}
	}
}

/**
 * Modal Form
 */
.modal-form {
	.custom-select-wrapper {
		.label {
			// @extend %input-element-styles;
			border-radius: 0;
			height: 32px;
		}

		.icon-dropdown-arrow {
			color: #000;
			z-index: 1;
		}

		.custom-select { z-index: 2; }
	}

	.legend {
		display: block;
		padding-top: 5px;
		border-top: 1px solid $grey-1;
		font-style: italic;
		font-size: calc-em(11px);

		.required { color: #f00; font-family: Verdana, Arial, Helvetica, sans-serif; }
	}

	.btn-group { margin-top: 25px; }

	.captcha-image {
		display: inline-block;
		width: 85%;
		vertical-align: middle;

		img { width: 100%; }
	}

	.captcha-refresh {
		display: inline-block;
		width: 12%;
		vertical-align: middle;
	}

	iframe {
		height: 100%;
		width: 100%;
		border: 0;
	}
}

/**
 * Subscribe modal
 */
.subscribe {
	display: none;
	width: 90%;
	max-width: 800px;
	margin: auto;

	h3 {
		margin-top: 50px;
		font-size: calc-em(26px);
		font-weight: 400;
		padding: 0 6px;
	}

	.btn-download {
		font-size: calc-em(16px);
		line-height: 1.4;
		margin: 0;
		color: $red;
		position: relative;
		display: inline-block;
		text-decoration: none;
		padding-right: 25px;
		padding: 0 25px 0 6px;
		word-break: break-all;

		i {
			font-size: 22px;
			text-decoration: none;
			position: absolute;
			right: 0;
			top: 0;
		}

		&:hover { text-decoration: underline; }
	}

	.col { box-sizing: border-box; }

	.input-group-text { margin-top: 0; }

	.btn-group {
		margin-top: 15px;
		padding: 0 6px;
	}
}


@media only screen and (max-width: $bpTablet - 1), print {
	.subscribe {
		.col {
			padding: 0 20px 15px;

			&:first-child {
				border-bottom: 1px solid darken(#f9f9f9, 10%);
			}
		}
	}
}


@media only screen and (min-width: $bpTablet), print {
	.subscribe {
		padding: 20px;

		h3 { margin-top: 0; }

		.col {
			padding: 0 20px 20px;

			&:first-child {
				border-right: 1px solid darken(#f9f9f9, 10%);
			}
		}
	}
}

@media only screen and (min-width: $bpDesktop), print{
	.subscribe {
		h3 { font-size: calc-em(24px); }
		.btn-download { font-size: calc-em(18px); }
	}
}


@media only screen and (min-width: $bpDesktop), print {
	.modal-form {
		margin: 0 160px;
	}
}
