// product-finder-results styles

.product-finder-results {
    .finder-results {
        &__desc {
            text-align: center;
            margin: 0 0 35px;

            h2 {
                margin-bottom: 0;
                font-size: calc-em(20px);
            }

            p {
                display: block;
                font-size: calc-em(16px);
                color: $darkgrey-2;
                font-weight: 300;
                margin: 0;
            }

        }
    }
}


@media only screen and (min-width: $bpTablet), print{
    .product-finder-results {
        .finder-results {
            &__desc {
                h2 {
                    font-size: calc-em(24px);
                }

                p {
                    font-size: calc-em(20px);
                }
            }
            
        }
    }

    .product-finder__empty {
        .empty-results__desc {
            width: 60%;
            margin: 0 auto;
        }
    }
}