// product-finder-compare styles

.product-finder-compare {
    &__footer {
        margin: 15px auto 0;
        position: relative;

        .compare__footer--back {
            display: block;
            // text-align: center;

            .button {
                // margin: 0 auto;
            }
        }

        .compare__footer--startover {
            position: absolute;
            right: 0;
            top: 0;
            width: auto;
        }

        button {
            line-height: 1;
            text-decoration: none;
            color: #fff;
            background: $red;
            position: relative;
            display: inline-block;
            padding: 5px 10px 5px 10px;
            border: 1px solid #D7D7D7;
            display: flex;
            align-items: center;
            cursor: pointer;
            border: 1px solid $red;
            font-family: $primaryFont;

            &:hover {
                background: white;
                color: $red;
                border: 1px solid $red;
            }
            span {
                font-size: calc-em(16px);
                font-weight: 500;
            }
            span + .icon {
                padding-left: 5px;
            }

            .icon + span {
                padding-left: 5px;
            }

            &.btn-white {
                color: black;
                background: white;
                border: 1px solid #D7D7D7;

                &:hover {
                    background: $red;
                    color: white;
                    border: 1px solid $red;
                }
            }

            .icon {
                font-size: 22px;
                line-height: 22px;
            }

            .icon-arrow-left, .icon-arrow-right {
                font-size: 14px;
            }

            .icon-return {
                font-size: 16px;
            }
        }
    }

    &__header {
        text-align: center;

        h1 {
            font-size: calc-em(28px);
        }
    }

    .spec-list {
        @include normalise-list;
        width: 20%;
        float: left;

        li {
            padding: 10px 15px;
            font-weight: 500;
            border-left: 1px solid $border3;

            &:first-child {
                border: 1px solid #fff;
            }

            &:last-child {
                border-bottom: 1px solid $border3;
            }

            span {
                font-size: calc-em(15px);
            }

            &:nth-child(even){
                background: $border;
            }
        }
    }

}

@media only screen and (min-width: $bpTablet), print{
    .product-finder-compare {

        &__header {

            h1 {
                font-size: calc-em(32px);
                margin-bottom: 35px;
            }
        }

        &__footer {
            margin: 35px auto 0;

            .compare__footer--back {
                // position: absolute;
                // left: 0;
            }

            .compare__footer--startover {
                position: absolute;
                right: 0;
                top: 0;
                width: auto;
            }
        }
    }
}
