// question-item styles
.question4 {
    .input-radio__item {
        display: none;

        &.show{
            display: block;
        }
    }
}
.question {
    &-item {
    
    }

    &__desc {
        margin: 0px 0 25px;

        p {
            margin: 0;

        }

        span {
            display: block;
            font-size: calc-em(13px);
            color: $darkgrey-2;
            font-weight: 300;
        }
    }

    &__input {
        width: 90%;
        margin: 0 auto;
        // display: flex;
        // justify-content: space-around;
        display: block;

        img {
            width: 90px;
            display: block;
            margin: 0 auto;
        }

        .input-radio, .input-checkbox {
            margin-bottom: 25px;
            
            label {
                display: block;
                text-align: center;
                cursor: pointer;
            }
            &__img {
                background: white;
                border: 1px solid white;
                padding: 20px 0;
            }

            input {
                visibility: hidden;
                position: absolute;
                
            }

            span {
                display: block;
                margin-bottom: 5px;
                font-weight: 500;
            }
        }

        .input-radio {
            input {
                &:checked {
                    + label {
                        .input-radio__img {
                            border: 1px solid $red;
                        }
                    }
                }
            }
        }

        .input-checkbox {
            input {
                &:checked {
                    + label {
                        .input-checkbox__img {
                            border: 1px solid $red;
                        }
                    }
                }
            }
        }

        &-list {
            @include normalise-list;
            width: 90%;
            margin: 0 auto;
            margin-bottom: 25px;

            li {
                background: white;
                border-bottom: 1px solid $border3;
                padding: 0 0 0 15px;

                &:last-child {
                    border-bottom: 0;
                }
            }

            input:checked {
                + label:after {
                    display: block;
                }
            }

            label {
                position: relative;
                display: inline-flex;
                align-items: center;
                cursor: pointer;
                padding: 10px 35px 10px 0;
                width: 100%;
                &:before {
                    content: "";
                    border: 1px solid $border4;
                    border-radius: 50px;
                    width: 22px;
                    height: 22px;
                    display: inline-block;
                    
                }

                &:after {
                    content: "";
                    width: 10px;
                    height: 10px;
                    background: $red;
                    border-radius: 50px;
                    position: absolute;
                    left: 6px;
                    display: none;
                }
                span {
                    font-size: calc-em(18px);
                    font-weight: 500;
                    margin-left: 10px;
                }
            }

            input {
                visibility: hidden;
                position: absolute;

            }
        }
    }
}

@media only screen and (min-width: $bpTablet), print{
    .question {
        &__input {
            width: 60%;

            &-checkbox {
                width: 100%;
            }

            .input-radio, .input-checkbox {
                margin-bottom: 0;

                span {
                    margin-bottom: 10px;
                }

                &__img {
                    padding: 30px 0;
                }
            }

            &-list {
                width: 40%;

                li {
                    padding: 0 0 0 35px;
                }
            }

            img {
                width: 120px;
            }
            
        }

        &__desc {
            margin: 30px 0 35px;
        }
    }
}

@media only screen and (min-width: $bpDesktop) {
    .question {
        &__input {

            &-checkbox {
                width: 90%;
            }

            &-list {
                width: 33.33333%;
            }
        }
    }

}