// new-wffm-form styles
.promotion {
	.page-header {
		h1 {
			margin: 0;
			font-size: calc-em(16px, 16px);
		}
	}

	p {
		margin: 0;
	}
}

.form-group {
	.help-block {
		font-size: calc-em(14px);
		display: block;
		margin-top: -1px;
	}

	.row {
		margin: 0 -6px;
	}
}

/* Required */
.required-field {
	position:relative;

	.control-label {
		&:before {
			display: none;
		}

		&:after{
			font-size: 20px;
			font-weight: 400;
			line-height: 1;
			color: $red;
			margin: 0 4px;
			content: "*";
		}
	}

	.checkbox {
		&:after {
			display: none;
		}

		&.required {
			&:after {
				display: inline !important;
				font-size: 20px;
				font-weight: 400;
				line-height: 1;
				color: $red;
				margin: 0 4px;
				content: "*";
			}
		}
	}
}

.field-validation-error, .field-validation-error span {
	color: $red;
}

.sitecore-form fieldset {
	margin: 30px 0;

	.row {
		margin: 0 -6px;
	}
}

//subscribe form
.subscribe-form {
	ul { @extend %list-unstyled; }

	form {
		margin: 0 -5px;
		padding: 0 5px;
	}

	label {
		display: none;
	}

	.form-submit-border {
		margin-top: 0;

		input {
			padding: 0;
			min-width: 100px !important;
		}
	}

	.form-group {
		width: 100%;
		margin-bottom: 10px !important;
	}
}


@media only screen and (min-width: $bpDesktop), print{
	.subscribe-form {
		form {
			padding: 0;
		}

		.form-group {
			float: left;
			margin-bottom: 0;
			margin-right: 5px !important;
			margin-left: 5px !important;

			&:nth-last-child(3), &:nth-last-child(4) {
				width: 18%;
			}

			&:nth-last-child(2) {
				width: 35%;
			}
		}

		.form-submit-border {
			float: left;
			margin-right: 5px !important;
			margin-left: 5px !important;
		}

	}

	//Captcha styles
	.g-recaptcha {
		.layout-side & {
			transform: scale(0.7);
			transform-origin: 0 0;
		}
	}
}
/* End Forms */


