// events checkbox table
.events-form-checklist {
    margin: 25px 0;

    .control-label {
        background-color: $red;
        color: #fff;
        padding: 15px;
        margin: 0;
        display: block;
    }

    .checkbox:nth-child(odd) {
        background-color: $grey-1;
    }

    .checkbox {
    	position: relative;
        margin: 0;
        padding: 15px 80px 15px 15px;

        label {
            position: relative;
            padding: 0;
            display: inline-block;
            width: 100%;

            input {
				margin: 0;
            }
        }

        & span {
            display: inline-block;
            vertical-align: top;
        }

        .event-form-time {
            padding-right: 37px;
        }

        .event-form-detail {
            width: 80%;
        }

        input[type="checkbox"] {
            position: absolute;
            top: calc(50% - 6.5px);
            left: calc(100% - 6.5px);
            margin-top: 0;
            margin-left: 41px;
        }

        &:after {
            content: '';
            clear: both;
            display: table;
            position: absolute;
            left: 53px;
            top: 12px;
        }
    }
}


input:not([type=file]),
input[type="text"],
input[type="password"],
input[type="email"],
input[type="search"],
input[type="number"],
select.form-control,
textarea.form-control {
	transition: box-shadow .2s;
	width: 100%;
	padding: 10px;
	border: 1px solid #c1c1c1;
	border-radius: 0;
	box-shadow: inset 0 1px 2px rgba(0,0,0,0.1);
	font-size: calc-em(16px);
}

input:not([type=file]),
input[type="text"],
input[type="password"],
input[type="email"],
input[type="search"],
input[type="number"],
select.form-control {
	font-family: $primaryFont;
	vertical-align: middle;
	height: 36px;
	margin: 2px 0;
	padding: 0 10px;

	&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
		color: $placeholder;
		font-weight: 300;
	}
	&::-moz-placeholder { /* Firefox 19+ */
		color: $placeholder;
		font-weight: 300;
	}
	&:-ms-input-placeholder { /* IE 10+ */
		color: $placeholder;
		font-weight: 300;
	}
	&:-moz-placeholder { /* Firefox 18- */
		color: $placeholder;
		font-weight: 300;
	}
}

select[size] {
	height: auto;
}

input[type="file"] {
	width: 100%;
	font-size: calc-em(16px);
}

input[type="checkbox"], input[type="radio"] {
	margin: 8px 10px 8px 0;
	border: none;
	box-shadow: none;
	transition: none;
	padding: 0;
	height: auto;
	width: auto;
}

input[type="text"], input[type="search"] {
	-webkit-appearance: none;
}

.form-submit-border {
	margin-top: 16px;
}

input[type="submit"] {
	font-family: $primaryFont;
	white-space: nowrap;
	min-width: 130px !important;
	height: 36px;
	margin: 2px 0;
	color: #fff;
	background: #d92231;
	border: 1px solid #d92231;
	font-weight: 400;
	line-height: 29px;
	text-align: center;
	position: relative;
	padding: 0 10px 0;
	cursor: pointer;
	width: auto;

	&:hover {
		color: $red;
		background-color: #fff;
		border: 1px solid $border4;

		&:after {
			color: $red;
		}
	}
}

form {
	button {
		font-family: $primaryFont;
		white-space: nowrap;
		min-width: 130px !important;
		height: 36px;
		margin: 2px 0;
		color: #fff;
		background: #d92231;
		border: 1px solid #d92231;
		font-weight: 400;
		line-height: 29px;
		text-align: center;
		position: relative;
		padding: 0 30px 0 10px;
		cursor: pointer;
		width: auto;

		&:hover {
			color: $red;
			background-color: #fff;
			border: 1px solid $border4;

			&:after {
				color: $red;
			}
		}

		&:after {
			font-family: Arial;
			font-size: 90%;
			color: #fff;
			position: relative;
			top: -1px;
			padding: 0 0 0 5px;
			content: "\0000a0\276F";
			transform: translate(0,-50%);
			position: absolute;
			top: 50%;
			right: 12px;
		}
	}

	&.search-bar {
		button {
			background-color: rgba(0, 0, 0, 0);
		}
	}
}


@media print {
	input[type="submit"] {
		border-color: #c1c1c1;
		color: #000;
	}
}
