// compare-item styles
.compare {
    &-list {
        @include normalise-list;
        @include clearfix;
        text-align: center;
    }

    &-item {
        width: 20%;
        float: left;

        &__header {
            text-align: center;
            padding: 25px 15px;

            h3 {
                margin: 10px 0 0px;
                line-height: 24px;
            }

            p {
                margin: 5px 0 0px;
                font-size: calc-em(14px);
                line-height: 18px;

                span {
                    display: block;
                }
            }

            > div {
                display: flex;
                align-items: center;
            }

            img {
                margin: 0 auto;
            }
        }

        &__content {
            border-right: 1px solid $border3;

            .spec {
                padding: 10px 15px;
                font-weight: 500;

                &:nth-child(odd){
                    background: $border;
                }

                span {
                    font-size: calc-em(15px);
                }
            }
        }

        &__footer {
            padding: 10px;

            a {
                position: relative;

                .icon {
                    position: absolute;
                    top: 50%;
                    right: 12px;
                    transform: translate(0, -50%);
                }
            }

            a.button {
                width: 100%;
                max-width: 200px;
                display: block;
                margin: 0 auto;
            }

            a.button--inverse {
                width: 100%;
                max-width: 200px;
                display: block;
                margin: 5px auto 5px;

                &:after {
                    display: none;
                }

                .icon-save {
                    margin-left: 5px;
                }
            }
        }
    }
}
@media only screen and (max-width: $bpDesktop - 1) {
    .compare {
        &-list {
            .slick-arrow {
                border: 1px solid $red;
                background: white;
                color: $red;
                padding: 35px 10px;
                position: fixed;
                top: 50%;
                transform: translateY(-50%);
                z-index: 999;
                visibility: visible;
                opacity: 1;
                transition: all 0.3s;

                span {
                    font-size: 26px;
                }

                &.slick-next {
                    right: 0;
                }

                &.slick-prev {
                    left: 0;
                }

                &.slick-disabled {
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }
        &-item__content .spec {
            .title {
                font-weight: 600;
                text-align: left;
            }

            span {
                display: block;
            }
        }
    }
}

@media only screen and (min-width: $bpDesktop), print{
    .compare {
        &-item {
            border: 1px solid $border3;
            border-right: 0;

            &:last-child {
                border-right: 1px solid $border3;
            }

            &__content {
                border: 0;
            }
        }
    }
}
